import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import noop from '@feizheng/noop';

const ENUM_CAPS = ['butt', 'round', 'square', 'inherit'];
const CLASS_NAME = 'react-svg-circle';

const arc = (x, y, r, s, f, rotate) => {
  const coords = (degrees) => {
    const radians = ((degrees - (90 - (rotate || 0))) * Math.PI) / 180.0;
    return {
      x: x + r * Math.cos(radians),
      y: y + r * Math.sin(radians)
    };
  };
  let start = coords(s),
    finish = coords(f);
  return [
    'M',
    start.x,
    start.y,
    'A',
    r,
    r,
    0,
    ~~(f - s > 180),
    1,
    finish.x,
    finish.y
  ].join(' ');
};

//lineCap: butt | round | square | inherit

export default class ReactSvgCircle extends Component {
  static displayName = CLASS_NAME;
  static version = '1.0.5';
  static propTypes = {
    /**
     * The extended className for component.
     */
    className: PropTypes.string,
    /**
     * The circle line-width.
     */
    lineWidth: PropTypes.number,
    /**
     * The circle line-cap.
     */
    lineCap: PropTypes.oneOf(ENUM_CAPS),
    /**
     * Circle deg, default 0.
     */
    value: PropTypes.number,
    /**
     * The change handler.
     */
    onChange: PropTypes.func
  };

  static defaultProps = {
    lineWidth: 10,
    lineCap: 'square',
    value: 0,
    onChange: noop
  };

  componentDidMount() {
    const { value } = this.props;
    this.update(value);
  }

  update(inValue) {
    const { lineWidth, onChange } = this.props;
    const r = 60 - lineWidth / 2;
    const value =
      inValue === 360
        ? arc(60, 60, r, 0, 359) + 'z'
        : arc(60, 60, r, 0, inValue);
    this.svg.setAttribute('d', value);
    onChange({ target: { value } });
  }

  shouldComponentUpdate(inNextProps) {
    const { value } = inNextProps;
    value !== this.props.value && this.update(value);
    return true;
  }

  render() {
    const {
      className,
      lineWidth,
      lineCap,
      value,
      onChange,
      svgExtra,
      svgProps,
      ...props
    } = this.props;
    return (
      <div
        data-component={CLASS_NAME}
        className={classNames(CLASS_NAME, className)}
        {...props}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          version="1.1"
          className={`${CLASS_NAME}__svg`}
          viewBox="0 0 120 120"
          {...svgProps}>
          <path
            ref={(svg) => (this.svg = svg)}
            strokeWidth={lineWidth}
            strokeLinecap={lineCap}
          />
          {svgExtra}
        </svg>
      </div>
    );
  }
}
